.labelText {
  height: 18px;
  font-size: 13px;
  font-family: SegoeUI;
  color: #edebe0;
  line-height: 17px;
  margin-top: 20px;
  margin-bottom: 5px;
  font-weight: 'normal';
}

.templateContentArea {
  margin-top: 10px;
  .zipFileSelectorArea {
    display: flex;
    position: relative;
    margin-bottom: 40px;
    .inputStyle {
      position: absolute;
      top:0;
      left:0;
      opacity: 0;
      z-index: 100000;
    }
    .buttonStyle {
      position: absolute;
      top:0;
      left:0;
    }
    .filePathSpan {
      position: absolute;
      left: 145px;
      top: 5px
    }
  }
}
