.newDialog {
  .content {
    width: 100%;
    height: 100%;
  }
  :global {
    .ms-Dialog-inner,
    .ms-Dialog-content {
      width: 100%;
      height: 100%;
      padding: 0;
    }
  }
  .newContent {
    width: 100%;
    height: 100%;
    display: flex;
    .switch {
      flex: 0 0 179px;
      background-color: #292929;
      overflow: hidden;
      .back {
        padding: 0 10px;
        display: flex;
        align-items: center;
        margin: 21px 0 26px;
        cursor: pointer;
        i {
          margin-right: 12px;
        }
      }
    }
    .selectContent {
      flex-grow: 1;
    }
  }
}
