.notFound {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: #2d2c2b;

  .notFoundImg {
    width: 328px;
    height: 327px;
    background-size: contain;
    background-image: url('@assets/static/img/notFoundPage/not-found-img.png');
  }

  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .notFoundImg {
      background-image: url('@assets/static/img/notFoundPage/not-found-img2x.png');
    }
  }

  .notFoundWord {
    height: 25px;
    margin: 57px 0 155px;
    color: #d2d2d2;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    white-space: nowrap;
    text-align: center;
  }

  .goBackLink {
    background-color: #3d80f1;
    border-radius: 4px;
    padding: 3px 18px;
  }
  .goBackBtn {
    width: 60px;
    height: 30px;
    background-image: url('@assets/static/img/notFoundPage/back.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }

  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .goBackBtn {
      background-image: url('@assets/static/img/notFoundPage/back2x.png');
    }
  }
}
