.lineContainer {
    padding: 0 49px 10px 49px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 27px;

    .rightContainer {
        display: flex;
        justify-content: center;
        align-items: center;

        .iconContainer {
            float: right;
            margin-right: 16px;
            display: flex;
            text-align: center;
            cursor: pointer;
            user-select: none;

            .icon {
                font-size: 22px;
                margin-right: 6px;
                color: red;
            }

            .deleteText {
                margin-top: 2px;
                font-size: 13px;
            }
        }
    }
}