@keyframes slide {
  from {
    left: 0;
  }

  to {
    left: 100%;
  }
}

.loading,
.partialLoading {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000001;
  .loadingBack {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
  }
  .loadingContent {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 2px;
  }
  .loadingContent {
    .slider {
      width: 2%;
      background-color: rgb(14, 112, 192);
      height: 2px;
      position: relative;
      animation-duration: 4s;
      animation-timing-function: linear;
      animation-fill-mode: both;
      animation-iteration-count: infinite;
      animation-name: slide;
      transform: translate3d(0, 0, 0);
    }
  }
}

.partialLoading {
  position: absolute;
}
