.templateList {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding-bottom: 21px;
  .templateHeader {
    height: 62px;
    border-bottom: 1px solid rgb(57, 60, 66);
    display: flex;
    align-items: center;
    padding: 0 21px;
    justify-content: space-between;
    flex: 0;
  }
  .templateTypeLine {
    position: relative;
    padding-left: 40px;
  }
  .templateListContent {
    flex-grow: 1;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 5px;
      height: 5px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: rgba(121, 121, 121, 0.4);
    }
    &::-webkit-scrollbar-track {
      background-color: #1a1c1e;
    }
  }
  .templatesParent {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    .icon {
      font-size: 30px;
      margin: 40px 143px 10px;
    }
    .import {
      text-align: center;
      font-weight: 600;
    }
  }

  .template {
    margin-left: 40px;
    margin-top: 21px;
    width: 317px;
    height: 138px;
    background: #292929;
    border-radius: 2px;
    cursor: pointer;
    .header {
      display: flex;
      align-items: center;
      padding: 18px 25px 9px 12px;

      img {
        width: 21px;
        height: 21px;
        margin-right: 12px;
        object-fit: contain;
      }
      .title {
        flex-grow: 1;
        font-weight: 600;
      }
    }
    .describe {
      padding-right: 30px;
      padding-left: 42px;
      color: #928e8a;
    }
    &:hover {
      background-color: #393a3b;
    }
  }
}
