.tree {
  :global {
    .list-row {
      height: 22px;
      line-height: 22px;
      cursor: pointer;
      &.cut-source {
        opacity: 0.5;
      }
      .fileNameEditor {
        width: 100%;
      }
      .ms-TextField > span {
        position: absolute;
        z-index: 999;
        width: 100%;
      }
      .ms-TextField-errorMessage {
        padding: 5px;
        color: #fff;
        white-space: normal;
        word-break: break-all;
        background-color: #4d1b1d;
        border: 1px solid rgb(164, 38, 44);
      }
      & > .inner {
        position: relative;
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        height: 100%;
        & > .indent {
          position: absolute;
          top: 0;
          left: 20px;
          height: 100%;
          pointer-events: none;
          & > .indent-guide {
            display: inline-block;
            box-sizing: border-box;
            height: 100%;
            border-left: 1px solid transparent;
            transition: border-color 0.1s linear;
          }
        }
        & > .twisted {
          flex-shrink: 0;
          height: 22px;
          font-size: 10px;
          i {
            margin-right: 8px;
            font-size: 12px;
            vertical-align: middle;
          }
        }
        & > .label {
          display: flex;
          flex: 1;
          justify-content: space-between;
          padding-right: 2px;
          overflow: hidden;
          font-weight: 400;
          white-space: nowrap;
          text-overflow: ellipsis;
          &::before {
            display: inline-block;
            width: 22px;
            margin-left: -5px;
            font-size: 22px;
            vertical-align: middle;
          }
          .labelContent {
            display: inline-block;
            align-self: center;
            max-width: 80%;
            overflow: hidden;
            line-height: 22px;
            text-overflow: ellipsis;
            vertical-align: middle;

            & > .prefix {
              display: inline-block;
              vertical-align: middle;
              line-height: 19px;
              padding: 0 6px 0 0;
            }
          }
          .labelAction {
            display: inline-block;
            margin-right: 24px;
            margin-left: 5px;
            line-height: 24px;
            .ms-Icon {
              vertical-align: middle;
            }
          }
        }
        & > .label.edit {
          display: flex;
          align-items: center;
          overflow: inherit;
        }
      }
    }
  }
}
