.importProject {
  width: 100%;
  height: 100%;

  .header {
    font-size: 13px;
    font-weight: 400;
    color: #edebe9;
    line-height: 18px;
    display: flex;
    align-items: center;
    padding: 54px 0 0 18px;
    .title {
      min-width: 75px;
      padding: 0 5px;
      height: 26px;
      text-align: center;
      line-height: 26px;
    }
    .uploadTitle {
      min-width: 75px;
      padding: 0 5px;
      height: 26px;
      text-align: center;
      line-height: 26px;
      background: #292929;
      margin: 0 14px 0 0;
      cursor: pointer;
    }

    .importTitle {
      min-width: 126px;
      padding: 0 5px;
      height: 26px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #292929;
      cursor: pointer;
      img {
        height: 20px;
        padding-right: 4px;
      }
    }
    .active {
      background: #106ebe;
    }
  }

  .uploadContent {
    height: 87%;
    padding: 35px 8.4% 0;
    position: relative;
    button {
      min-width: 75px;
      height: 26px;
      margin-top: 23px;
      background: #292929;
      color: #d8d7d7;
      border: none;
      border-radius: 2px;
      margin-right: 24px;
      cursor: pointer;
    }
    button:nth-child(1) {
      background: #106ebe;
    }
    button:hover {
      background: #1c88c9;
    }
    .upload {
      height: 262px;
      background: #292929;
      .icon {
        display: flex;
        justify-content: center;
        padding-top: 83px;
        position: relative;
        i {
          font-size: 28px;
          cursor: pointer;
        }
        .fileInput {
          position: absolute;
          width: 100%;
          height: 262px;
          top: 0;
          opacity: 0;
          cursor: pointer;
        }
      }
      .describtion {
        margin-top: 11px;
        text-align: center;
      }
    }
    .tips {
      height: 21px;
      margin-top: 13px;
      display: flex;
      align-items: center;
      img {
        height: 21px;
        margin-right: 10px;
      }
    }
    .footer {
      position: absolute;
      left: 8.4%;
      right: 8.4%;
      bottom: 66px;
      border-top: 1px solid #2d2f31;
    }
  }

  .importContent {
    height: 87%;
    padding: 39px 8.4% 0;
    position: relative;
    textarea {
      width: 100%;
      height: 152px;
      resize: none;
      border: none;
      outline: none;
      color: #edebe9;
      border-radius: 2px;
      padding-left: 13px;
      background: #292929;
    }

    button {
      min-width: 75px;
      height: 26px;
      margin-top: 23px;
      background: #292929;
      color: #d8d7d7;
      border: none;
      border-radius: 2px;
      margin-right: 24px;
      cursor: pointer;
    }
    button:nth-child(1) {
      background: #106ebe;
    }
    button:hover {
      background: #1c88c9;
    }
    .footer {
      position: absolute;
      left: 8.4%;
      right: 8.4%;
      bottom: 66px;
      border-top: 1px solid #2d2f31;
    }
  }
}
