.navigatorParent {
  width: 407px
}
.navigationControl {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #ccc;
}
.arrow{
  width: 20px;
  height: 41px;
  position: absolute;
  z-index:  1;
  img{
    width: 8px;
    height: 13px;
  }
  img:nth-child(2){
    display: none;
  }
  .leftArrow{
    position: absolute;
    width: 20px;
    height: 41px;
    background: #2A2B2B;
    display: flex;
    justify-Content: center;
    align-items: center;
    transition: opacity 2s;
  }
  .rightArrow{
    position: absolute;
    width: 20px;
    height: 41px;
    background: #106EBE;
    display: flex;
    justify-Content: center;
    align-items: center;
    transition: opacity 2s;
  }
  .leftArrow:hover{
    cursor: pointer;
    background:#3F3F3F;
  }
}


.rightArrow:hover{
  cursor: pointer;
  background:#1194CE;
}

@media (-webkit-min-device-pixel-ratio: 2),
  (min--moz-device-pixel-ratio: 2),
  (-o-min-device-pixel-ratio: 2),
  (min-device-pixel-ratio: 2) {
    .arrow{
      img:nth-child(1){
        display: none;
      }
      img:nth-child(2){
        display: block;
      }
    }
}
