@FONT_LEVEL_ONE: #edebe9;
@INPUT_DISABLE_BACKGROUND: #595959;
@DEFAULT_BACKGROUND: #2d2d2d;

.ms-Button {
  color: @FONT_LEVEL_ONE;
  .ms-Button-textContainer {
    flex-grow: initial;
  }

  &:hover {
    color: @FONT_LEVEL_ONE;
  }

  &:disabled {
    color: @FONT_LEVEL_ONE;
  }
}

.ms-Button--primary {
  height: 26px;
  line-height: 26px;
  margin: 0;

  &:hover {
  }

  &:disabled {
    background-color: @INPUT_DISABLE_BACKGROUND;
  }
}

.ms-Button--default {
  height: 26px;
  line-height: 26px;
  background-color: @DEFAULT_BACKGROUND;
  border-color: @DEFAULT_BACKGROUND;

  &:hover {
    background-color: @DEFAULT_BACKGROUND;
  }

  &:disabled {
    background-color: @INPUT_DISABLE_BACKGROUND;
  }
}

.ms-Button--icon {
  height: 26px;
  line-height: 26px;

  a &:hover {
    background-color: @DEFAULT_BACKGROUND;
  }

  &:disabled {
    background-color: @INPUT_DISABLE_BACKGROUND;
  }
}

.ms-TextField-fieldGroup {
}

.ms-TextField-fieldGroup,
.ms-TextField-field {
  height: 22px;
  line-height: 22px;
  background-color: #393a3b;
  border: none;

  &:visited {
  }
}

.ms-ComboBox {
  background-color: #393a3b;
  border-radius: 2px;
  height: 22px;
  line-height: 22px;
}
.ms-ComboBox:after {
  border: 0;
}

.ms-ComboBox .ms-ComboBox-Input {
  background-color: #393a3b;
}

.ms-Checkbox-checkbox {
  background: none;

  .ms-Checkbox-checkmark {
    color: #106ebe;
  }
}
.ms-Checkbox-checkbox:hover {
  background: none;
}

.ms-Dropdown-title {
  border-style: none;
  height: 26px;
  line-height: 24px;
  background-color: #393a3b;
}

.ms-Dropdown-caretDownWrapper {
  height: 26px;
  line-height: 26px;
}

.ms-Link {
  color: rgb(55, 148, 255);
  outline: none;
  text-decoration: underline;
  user-select: none;
  &:hover,
  &:active,
  &:focus,
  &:active:hover {
    color: rgb(55, 148, 255);
  }
}

.ms-TextField-fieldGroup,
.ms-TextField-field {
  height: 22px;
  line-height: 22px;
  background-color: #393a3b;
  border: none;
}

.ms-TextField-field {
  font-size: 13px;
  font-weight: 400;
  color: rgb(237, 235, 233);
  box-shadow: none;
  margin: 0px;
  padding: 0px 8px;
  box-sizing: border-box;
  border-radius: 0px;
  border: none;
  background: none transparent;
  width: 100%;
  min-width: 0px;
  text-overflow: ellipsis;
  outline: 0px;
}

.ms-GroupedList {
  font-size: 12px;
  font-weight: 400;
  position: relative;
  .ms-GroupedList-group {
    transition: background-color 0.267s cubic-bezier(0.445, 0.05, 0.55, 0.95) 0s;
    .ms-List-cell {
      min-height: auto;
    }
  }
}
