.newContent {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #EDEBE9;
    position: relative;
    overflow: hidden;
    cursor: pointer;

    .newIcon {
        font-size: 30px;
        margin-bottom: 30px;
    }

    &:hover {
        background-color: rgba(52, 52, 53, 0.7);
    }
}