.templateContent {
    width: 100%;
    height: 100%;
    position: relative;
    cursor: pointer;

    &:hover {
        background-color: #333334;
    }

    .checkoutContainer {
        position: absolute;
        right: 10px;
        top: 11px;
        z-index: 1;
    }

    .icon {
        position: absolute;
        right: 16px;
        bottom: 16px;

        img {
            width: 20px;
            height: 20px;
            object-fit: contain;
        }
    }

    .preview {
        width: 287px;
        height: 160px;
        background: #1B1C1E;
        margin: 17px 16px 5px;
        overflow: hidden;

        img {
            width: 100%;
        }
    }

    .name {
        padding-left: 16px;
        padding-right: 62px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        word-break: break-all;
        margin-top: 12px;
        font-weight: bold;
    }

    .descContainer {
        display: flex;
        align-items: center;
        width: 80%;
        height: 80%;
        justify-content: center;
        margin-left: 10%;

        .desc {
            width: 100%;
            font-size: 14px;
            color: gray;
        }
    }


    .version {
        position: absolute;
        left: 16px;
        bottom: 16px;
    }

    .templateOperator {
        display: none;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgba(52, 52, 53, 0.7);
    }

    &:hover {
        .templateOperator {
            display: block;
        }
    }
}