.leftItem {
  margin: 0 28px 25px;
  a {
    text-decoration: none;
  }
  .header {
    height: 150px;
    border-bottom: 1px solid #373535;
    .logo {
      width: 52px;
      margin: 40px auto 13px;
      img {
        width: 52px;
      }
      img:nth-child(2) {
        display: none;
      }
    }
    .headerTitle {
      height: 24px;
      font-size: 22px;
      font-weight: 600;
      color: #edebe9;
      line-height: 30px;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .headerDescription {
      font-size: 13px;
      color: #bebbb8;
      line-height: 17px;
      text-align: center;
      margin-top: 5px;
    }
  }
  .content {
    margin-top: 25px;
    .contentTitle {
      font-size: 13px;
      color: #edebe9;
      line-height: 17px;
      margin-bottom: 15px;
    }
    .contentItem {
      height: 102px;
      margin-bottom: 30px;
      display: flex;
      .contentImg {
        width: 160px;
        height: 102px;
        border-radius: 2px;
        background: #045a80;
        display: flex;
        justify-content: center;
        align-items: center;
        img:nth-child(2) {
          width: 47px;
          height: 53px;
        }
        img:nth-child(2) {
          width: 52px;
          height: 51px;
          display: none;
        }
      }

      .contentMessage {
        width: 175px;
        margin-left: 9px;
        display: flex;
        .contentMessageTitle {
          font-size: 10px;
          color: #edebe9;
          line-height: 17px;
          margin-bottom: 10px;
        }
        .contentDescription {
          font-size: 13px;
          color: #8a8a8a;
          line-height: 18px;
        }
      }
    }
    .contentItem:hover {
      .contentImg {
        background: #1194ce;
      }
      .contentDescription {
        text-decoration: underline;
      }
    }
  }
  .footer {
    height: calc(100vh - 550px);
    min-height: 85px;
    position: relative;
    .footerItem {
      width: 100%;
      padding-left: 39px;
      display: flex;
      flex-wrap: wrap;
      position: absolute;
      bottom: 10px;
      .footerLogo {
        width: 37px;
        height: 38px;
        border-radius: 50%;
        background: #424242;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 20px 15px 0;
        margin-top: 2px;
        a img {
          width: 36px;
          margin-right: 2px;
        }
        .QRCode {
          width: 110px;
          height: 110px;
          border: 5px solid white;
          margin-left: 15px;
          background-color: white;
          display: none;
          position: absolute;
          bottom: 125px;
        }
        a img:nth-child(2) {
          display: none;
        }
      }
      .footerLogo:hover {
        background: #106ebe;
        .QRCode {
          display: block;
        }
      }
    }
  }
  .version {
    margin: 4px 0;
    font-size: 13px;
    color: #8a8a8a;
    line-height: 17px;
  }
}

@media (-webkit-min-device-pixel-ratio: 2),
  (min--moz-device-pixel-ratio: 2),
  (-o-min-device-pixel-ratio: 2),
  (min-device-pixel-ratio: 2) {
  .logo {
    img:nth-child(1) {
      display: none;
    }
    img:nth-child(2) {
      display: block !important;
    }
  }
  .footerLogo {
    a img:nth-child(1) {
      display: none;
    }
    a img:nth-child(2) {
      display: block !important;
    }
  }
  .contentImg {
    img:nth-child(1) {
      display: none;
    }
    img:nth-child(2) {
      display: block !important;
    }
  }
}
