@import url('./theme.less');

* {
  box-sizing: border-box;
}
body {
  font-size: 13px;
}

.hidden {
  display: none !important;
}

&::-webkit-scrollbar {
  width: 15px;
  height: 5px;
}
&::-webkit-scrollbar-thumb {
  background-color: rgba(121, 121, 121, 0.4);
}
&::-webkit-scrollbar-track {
  background-color: #1a1c1e;
}
