.errorPage {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 100000;
  background-color: #1b1c1e;
  h3 {
    display: flex;
    align-items: center;
    justify-content: center;
    span {
      margin-left: 15px;
    }
  }
  button {
    margin-top: 32px;
  }
  button:last-child {
    margin-left: 15px;
  }
}
