.projectContent {
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  .icon {
    position: absolute;
    right: 16px;
    bottom: 16px;
    img {
      width: 20px;
      height: 20px;
      object-fit: contain;
    }
  }
  .preview {
    width: 287px;
    height: 160px;
    background: #1B1C1E;
    margin: 17px 16px 5px;
    overflow: hidden;
    img {
      width: 100%;
    }
  }
  .name {
    padding-left: 16px;
    padding-right: 62px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: break-all;
    margin-top: 8px;
  }
  .date {
    padding: 0 16px;
    margin-top: 5px;
    color: #A4A4A3;
  }

  .projectOperator {
    display: none;
    position: absolute;
    top:0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(52, 52, 53, 0.7);
  }
  &:hover {
    .projectOperator {
      display: block;
    }
  }
}
