.sign-in-button {
  width: 100%;
  cursor: pointer;
  padding: 8px 23px;
  background: #292929;
  border-radius: 2px;
  .sign-in-button-inner {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    img {
      margin-right: 24px;
    }
    :global {
      .ms-Spinner {
        position: absolute;
        left: 0;
      }
    }
  }
}

.active {
  background: #0e62ac;
}

&.sign-in-button:hover {
  background: #0e62ac;
}
