.templateOperator {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;

    .operatorList {
        background: #1D1D1D;
        border-radius: 2px;
        opacity: 0.71;
        display: flex;
        overflow: hidden;

        &:hover>.operator>.split {
            display: none;
        }

        .operator {
            width: 60px;
            height: 60px;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;

            .split {
                position: absolute;
                left: 0;
                top: 50%;
                margin-top: -16px;
                width: 1px;
                height: 32px;
                background-color: #3a3939;
            }

            .openIcon {
                font-size: 25px;
                height: 30px;
            }

            .deleteIcon {
                font-size: 22px;
            }

            .editIcon {
                font-size: 20px;
            }

            i {
                font-size: 30px;
            }

            &:hover {
                background-color: #106EBE;
            }
        }

    }
}