.projects {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
  align-content: flex-start;
  padding: 0 36px 56px 56px;

  .projectItem {
    display: flex;
    width: 320px;
    height: 233px;
    margin-right: 20px;
    margin-bottom: 28px;
    background-color: #2e3033;
  }

  .projectEmpty {
    width: 320px;
    margin-right: 20px;
    height: 0px;
  }
}
