.scroll {
  height: 100%;
  scroll-behavior: smooth;

  :global {
    .rcs-custom-scroll {
      min-width: 0;
      min-height: 0;
      margin-right: -1px;
    }
    .rcs-custom-scroll .rcs-outer-container {
      overflow: hidden;
    }
    .rcs-custom-scroll .rcs-outer-container .rcs-positioning {
      position: relative;
    }
    .rcs-custom-scroll .rcs-outer-container:hover .rcs-custom-scrollbar {
      opacity: 1;
      transition-duration: 0.2s;
    }
    .rcs-custom-scroll .rcs-inner-container {
      overflow-x: hidden;
      overflow-y: scroll;
      -webkit-overflow-scrolling: touch;
    }
    .rcs-custom-scroll .rcs-inner-container:after {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      height: 0;
      background-image: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.2) 0%,
        rgba(0, 0, 0, 0.05) 60%,
        rgba(0, 0, 0, 0) 100%
      );
      transition: height 0.1s ease-in;
      content: '';
      pointer-events: none;
      will-change: height;
    }
    .rcs-custom-scroll .rcs-inner-container.rcs-content-scrolled:after {
      height: 5px;
      transition: height 0.15s ease-out;
    }
    .rcs-custom-scroll.rcs-scroll-handle-dragged .rcs-inner-container {
      user-select: none;
    }
    .rcs-custom-scroll .rcs-custom-scrollbar {
      position: absolute;
      right: 0;
      z-index: 1;
      box-sizing: border-box;
      width: 14px;
      height: 100%;
      padding: 6px 0;
      opacity: 0;
      transition: opacity 0.4s ease-out;
      pointer-events: none;
      will-change: opacity;
    }
    .rcs-custom-scroll .rcs-custom-scrollbar.rcs-custom-scrollbar-rtl {
      right: auto;
      left: 3px;
    }
    .rcs-custom-scroll.rcs-scroll-handle-dragged .rcs-custom-scrollbar {
      opacity: 1;
    }
    .rcs-custom-scroll .rcs-custom-scroll-handle {
      position: absolute;
      top: 0;
      width: 100%;
    }
    .rcs-custom-scroll .rcs-inner-handle {
      height: calc(100% - 12px);
      margin-top: 6px;
      background-color: rgba(121, 121, 121, 0.4);
      border-radius: 0;
    }
    ::-webkit-scrollbar {
      width: 14px;
      height: 14px;
    }
    ::-webkit-scrollbar-track {
      border-radius: 0;
    }
    ::-webkit-scrollbar-thumb {
      height: calc(100% - 12px);
      margin-top: 6px;
      border-radius: 0;
      &:hover {
        opacity: 0;
        transition-duration: 0.2s;
        background-color: rgba(121, 121, 121, 0.4);
      }
    }
    * {
      scrollbar-color: rgba(121, 121, 121, 0.4) transparent;
      scrollbar-width: 14px;
    }
  }
}
